import { fetchVideos } from '@/features/posts/post.server';
import VideoCard from '@/features/videos/components/VideoCard';
import { VideoListResponse } from '@/features/videos/video.type';

export default async function VideosPage() {
  const videos: VideoListResponse[] = await fetchVideos();

  return (
    <div className="max-w-7xl mx-auto px-6 py-12">
      <h1 className="text-3xl font-medium text-center mb-4 text-gray-700">みんなの学習メモ</h1>
      <p className="text-center text-sm text-gray-500 mb-12">
        YouTubeを見て学んだことを、気軽に残しています
      </p>

      <ul className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3 items-stretch">
        {videos.map((video) => (
          <li key={video.id}>
            <VideoCard video={video} />
          </li>
        ))}
      </ul>
    </div>
  );
}
